export const HEADING_SET = Symbol('HEADING_SET');
export const HEADING_TRANSFORM = Symbol('HEADING_TRANSFORM');

export function setHeading(heading: string) {
  return {
    type: HEADING_SET,
    payload: { heading },
  };
}

export function transformHeading(heading: string) {
  return {
    type: HEADING_TRANSFORM,
    payload: { heading },
  };
}
